.navbar {
  display: flex;
  width: 100%;
  height: 60px;
  cursor: pointer;
}


.navbar-item {
  display: flex;
  width: 30%;
  height: 60px;

  gap: 5px;

  background-color: rgba(250, 235, 215, 0.607);
  border-radius: 20px 0px 0px 0px;
  color: green;

  align-items: center;
  justify-content: center;
  transition: all .3s ease;
}

.navbar-item:hover {
  background-color: bisque;
}